<!--  -->
<template>
<div class='login'>
    <!-- bg effect -->
	<div id="bg">
		<canvas></canvas>
		<canvas></canvas>
		<canvas></canvas>
	</div>
	<div class="sub-main-w3" style="position: absolute;;top:15%;left:25%">
		<div class="form">
			<h2>
				<i class="fa fa-long-arrow-down"></i>
			</h2>
			<div class="form-style-agile">
				<label>
					<i class="fa fa-user"></i>
					
				</label>
				<input placeholder="" name="Name" type="text"v-model="username">
			</div>
			<div class="form-style-agile">
				<label>
					<i class="fa fa-unlock-alt"></i>
					
				</label>
				<input placeholder="" name="Password" type="password" v-model="password">
			</div>
			<!-- checkbox -->
			<div class="wthree-text">
				<ul>
					<li>
						<label class="anim" style="display:none">
							<input type="checkbox" class="checkbox" :checked="savePassWrold">
							<span></span>
						</label>
					</li>
					<li>
						<a href="#">?</a>
					</li>
				</ul>
			</div>
			<!-- //checkbox -->
				<input type="submit" value="Log In">
		</div>
			
	</div>
	<!-- //content -->
</div>
</template>

<script>
//jsjsjson
//import  from '';
import "../../../static/css/style.css"
import miaov from '../../../static/js/miaov'

export default {
//import
components: {},
data() {
//
return {
    savePassWrold :true,
	username:"",
	password:""
};
},
// data
computed: {},
//data
watch: {},
//
methods: {

		setCookie(name,value){
		var Days = 30;
		var exp = new Date();
		exp.setTime(exp.getTime() + Days*24*60*60*1000);
		document.cookie = name + "="+ value + ";expires=" + exp.toString();
		}	
},
// - this
created() {

},
// - DOM
mounted() {
var that  = this
miaov.loginBgAnimation()
$("input[type=submit]").click(function(){
	let postData = {
					"username":that.username,
					"password":that.password
					}

	 that.$getData.login(`/login`,"post",postData)
	 .then(data=>{
		 if(data.data.msg == ""){
			 that.setCookie("token",data.data.token)
			 that.$router.push({path:'/'})
		 }else{
			 that.$message.error(data.data.msg);
		 }
	 })
	 .catch(e=>{
		 console.log(e)
	 })

    // 
})
    

		// addEventListener("load", function () {
        
		// 	// setTimeout(hideURLbar, 0);
		// }, false);

		// function hideURLbar() {
		// 	window.scrollTo(0, 1);
		// }

        // $("#app").css("background","none")
},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
</script>
<style scoped>

</style>


